<template>
  <nav :class="[$style.tabbar, $style.tabbarFixed]" class="van-hairline--top" :style="{ position: 'fixed', bottom: 0, zIndex: 99}">
    <slot/>
  </nav>
</template>

<script>
export default {
  name: 'base-tabbar'
}
</script>

<style module>
.tabbar{
  width: 100%;
  height: 50px;

  background-color: #ffffff;
  display: flex;
  flex-direction: row;
}
</style>



