<template>
  <div :class="$style.root">
    <v-tabbar/>
    <keep-alive>
      <router-view :class="$style.padding"/>
    </keep-alive>
  </div>
</template>

<script>
import Tabbar from './Tabbar.vue'
export default {
  name: 'page-root',
  components: {
    [Tabbar.name]: Tabbar
  }
}
</script>

<style module>
.root{
  width: 100%;
}
.padding{
  box-sizing: content-box;
  padding-bottom: 50px;
}
</style>



