<template>
  <router-link tag="div" :to="to" :exact="true" :exact-active-class="$style.active" :class="$style.flex">
    <div :class="$style.icon">
      <slot name="icon" :active="active"/>
    </div>
    <div :class="$style.text">
      <slot/>
    </div>
  </router-link>
</template>

<script>
export default {
  name: 'base-tabbar-item',
  props: {
    to: {
      type: String,
      required: true
    }
  },
  data () {
    return {
    }
  },
  computed: {
    active () {
      return this.$route.fullPath === this.to
    }
  }
}
</script>

<style module>
.flex{
  flex: 1;
  line-height: 1;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.active{
  color: #3388ff;
}
.text{
  font-size: 14px;
}
</style>



