<template>
  <base-tabbar>
    <base-tabbar-item to="/">
      <template slot="icon" slot-scope="props">
        <i class="iconfont icon-home" :class="{'icon-homefill': props.active}"/>
      </template>
      <span>首页</span>
    </base-tabbar-item>
    <base-tabbar-item to="/category">
      <template slot="icon" slot-scope="props">
        <i class="iconfont icon-cart" :class="{'icon-cartfill': props.active}"/>
      </template>
      <span>分类</span>
    </base-tabbar-item>
    <base-tabbar-item to="/message">
      <template slot="icon" slot-scope="props">
        <i class="iconfont" :class="{'icon-communityfill': props.active, 'icon-community': !props.active}"/>
      </template>
      <span>消息</span>
    </base-tabbar-item>
    <base-tabbar-item to="/me">
      <template slot="icon" slot-scope="props">
        <i class="iconfont icon-my" :class="{'icon-myfill': props.active}"/>
      </template>
      <span>我的</span>
    </base-tabbar-item>
  </base-tabbar>
</template>

<script>
import { Tabbar, TabbarItem } from './Tabbar/index.js'
export default {
  name: 'v-tabbar',
  components: {
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem
  }
}
</script>

<style module>

</style>



